import React from "react";
import { ErrorContent } from "../components";

const Error404Page = () => {
  const code = 404;
  const message = "Oops! Page not found!";
  const description = "The page you requested was not found.";

  return (
    <ErrorContent
      code={code}
      message={message}
      description={description} />
    );
}

export default Error404Page;